<template>
  <v-container>
    <v-card class="mx-auto px-5 py-10 mt-10" max-width="500">
      <h1 class="display-1 my-5" align="center">Chackit</h1>
      <v-form v-model="form" @submit.prevent="onSubmit" id = "login-form">
        <v-text-field
            v-model="username"
            name="username"
            :readonly="loading"
            :rules="[required]"
            class="mb-2"
            label="ÏïÑÏù¥Îîî"
            clearable
        ></v-text-field>

        <v-text-field
            v-model="password"
            name="password"
            :readonly="loading"
            :rules="[required]"
            label="ÎπÑÎ∞ÄÎ≤àÌò∏"
            placeholder="Enter your password"
            type="password"
            clearable
        ></v-text-field>

        <br />

        <v-btn
            :disabled="!form"
            :loading="loading"
            color="black white--text"
            size="large"
            type="submit"
            variant="elevated"
            block
            @click="login()"
        >
          Î°úÍ∑∏Ïù∏
        </v-btn>
      </v-form>

      <v-card-text class="text-center">
        <button
            class="text-grey text-decoration-none"
            @click.stop="dialog = true"
        >
          ÌöåÏõêÍ∞ÄÏûÖ
        </button>
      </v-card-text>
    </v-card>
  </v-container>

  <!-- ----------------------------------------------------------------------------------------------------------- -->
  <!-- ÌöåÏõêÍ∞ÄÏûÖ dialog -->
  <v-dialog v-model="dialog" max-width="500">
    <v-card class="text-center pa-4">
      <h1 class="display-1 my-5" align="center">Join Us</h1>
      <v-form @submit.prevent="onSubmit" id="register-form" ref="registerForm">
        <v-text-field
            name="username"
            :readonly="loading"
            :rules="[required]"
            class="mb-2"
            label="ÏïÑÏù¥Îîî"
            placeholder="Enter your id"
            clearable
            :error-messages="errors.username"
        ></v-text-field>

        <v-text-field
            name="password1"
            :readonly="loading"
            :rules="[required]"
            label="ÎπÑÎ∞ÄÎ≤àÌò∏"
            placeholder="Enter your password"
            type="password"
            clearable
            :error-messages="errors.password1"
        ></v-text-field>
        <v-text-field
            name="password2"
            :readonly="loading"
            :rules="[required]"
            label="ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏"
            placeholder="Enter your password"
            type="password"
            clearable
            :error-messages="errors.password2"
        ></v-text-field>

        <v-text-field
            name="email"
            :readonly="loading"
            :rules="[required]"
            label="Ïù¥Î©îÏùº"
            placeholder="Enter your email"
            type="email"
            clearable
            :error-messages="errors.email"
        ></v-text-field>

        <br />

        <v-btn
            :loading="loading"
            color="black white--text"
            size="large"
            type="submit"
            variant="elevated"
            block
            @click="register()"
        >
          ÌöåÏõêÍ∞ÄÏûÖ
        </v-btn>
        <v-card-text class="text-center">
          <button
              class="text-grey text-decoration-none"
              @click="dialog = false"
          >
            Ï∑®ÏÜå
          </button>
        </v-card-text>
      </v-form>
    </v-card>
  </v-dialog>
</template>

<script>
import axios from "axios";
// CSRF ÌÜ†ÌÅ∞
axios.defaults.xsrfCookieName = "csrftoken";
axios.defaults.xsrfHeaderName = "X-CSRFToken";

export default {
  data: () => ({
    form: false,
    username: null,
    password: null,
    loading: false,
    dialog: false,
    me: {}, // ÏùëÎãµ user Ï†ïÎ≥¥
    errors: {}, // ÌöåÏõêÍ∞ÄÏûÖ Í∞Å ÌïÑÎìúÏùò Ïò§Î•ò Î©îÏãúÏßÄ Ï†ÄÏû•
  }),

  methods: {
    onSubmit() {
      if (!this.form) return

      this.loading = true

      setTimeout(() => (this.loading = false), 2000)
    },
    required(v) {
      return !!v || 'Field is required'
    },

    // Î°úÍ∑∏Ïù∏
    login() {
      console.log("login() Ìò∏Ï∂ú");
      const postData = new FormData(document.getElementById("login-form"));

      axios.post("/api/login/", postData)
          .then(res => {
            console.log("login() ÏÑ±Í≥µ", res);
            this.me = res.data; // DjangoÏóêÏÑú Î≥¥ÎÇ¥Ï§Ä user Ï†ïÎ≥¥
            window.location.href = "/"; // ÌôàÏúºÎ°ú Î¶¨Îã§Ïù¥Î†âÌä∏
          })
          .catch(err => {
            console.log("login() Ïã§Ìå®", err);
            alert("ÏïÑÏù¥Îîî, ÎπÑÎ∞ÄÎ≤àÌò∏Î•º ÌôïÏù∏ÌïòÏÑ∏Ïöî.");
          });
    },

    // ÌöåÏõêÍ∞ÄÏûÖ
    register() {
      console.log("register() Ìò∏Ï∂ú");
      this.errors = {}; // Ïò§Î•ò Î©îÏãúÏßÄ Ï¥àÍ∏∞Ìôî
      const postData = new FormData(document.getElementById("register-form"));

      axios.post("/api/account/create/", postData, {headers : {"X-CSRFToken": this.csrfToken}})
          .then(res => {
            console.log("register() ÏÑ±Í≥µ", res);
            console.log("username >> ", res.data.username);
            this.dialog = false; // dialog Ï∞Ω Îã´Í∏∞
          })
          .catch(err => {
            console.log("register() Ïã§Ìå®", err);
            if (err.response && err.response.data.errors) {
              const errorData = err.response.data.errors;
              // ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏùÄ Ïò§Î•ò Î©îÏãúÏßÄÎ•º errors ÌïÑÎìúÏóê Ìï†Îãπ
              this.errors.username = errorData.username ? errorData.username.map(e => e.message) : [];
              this.errors.password1 = errorData.password1 ? errorData.password1.map(e => e.message) : [];
              this.errors.password2 = errorData.password2 ? errorData.password2.map(e => e.message) : [];
              this.errors.email = errorData.email ? errorData.email.map(e => e.message) : [];
            } else {
              alert("ÌöåÏõêÍ∞ÄÏûÖ Ïã§Ìå®:(");
            }
          });
    },
  },
}
</script>